import { NextRequest, NextResponse } from 'next/server';
import { auth } from '@clerk/nextjs/server';
import { createInvoice, listInvoices } from '@/services/invoiceService';
import { InvoiceCreateSchema, InvoiceListQuerySchema } from '@/lib/validations';
import { handleApiError, errors } from '@/utils/error';
import { checkRateLimit, ratelimit } from '@/lib/ratelimit';
import { logApiRequest, logApiResponse } from '@/utils/logger';

// Force dynamic rendering (no caching)
export const dynamic = 'force-dynamic';

/**
 * POST /api/invoices - Create new invoice
 */
export async function POST(req: NextRequest) {
  const startTime = Date.now();
  const method = 'POST';
  const path = '/api/invoices';

  try {
    // Authenticate
    const { userId } = await auth();
    if (!userId) {
      throw errors.unauthorized();
    }

    logApiRequest(method, path, userId);

    // Rate limit
    const { success } = await checkRateLimit(userId, ratelimit);
    if (!success) {
      throw errors.rateLimit();
    }

    // Parse and validate request body
    const body = await req.json();
    const validated = InvoiceCreateSchema.parse(body);

    // Create invoice
    const invoice = await createInvoice({
      freelancerId: userId,
      clientName: validated.clientName,
      clientEmail: validated.clientEmail,
      amount: validated.amount,
      dueDate: new Date(validated.dueDate),
      description: validated.description,
      paymentMethods: validated.paymentMethods,
    });

    const duration = Date.now() - startTime;
    logApiResponse(method, path, 201, duration, userId);

    return NextResponse.json(
      {
        invoiceId: invoice.invoiceId,
        reference: invoice.reference,
        invoice,
      },
      { status: 201 }
    );
  } catch (error) {
    return handleApiError(error);
  }
}

/**
 * GET /api/invoices - List invoices with filters
 */
export async function GET(req: NextRequest) {
  const startTime = Date.now();
  const method = 'GET';
  const path = '/api/invoices';

  try {
    // Authenticate
    const { userId } = await auth();
    if (!userId) {
      throw errors.unauthorized();
    }

    logApiRequest(method, path, userId);

    // Rate limit
    const { success } = await checkRateLimit(userId, ratelimit);
    if (!success) {
      throw errors.rateLimit();
    }

    // Parse query parameters
    const { searchParams } = new URL(req.url);
    const query = {
      status: searchParams.get('status') || undefined,
      limit: searchParams.get('limit') || undefined,
      offset: searchParams.get('offset') || undefined,
    };

    const validated = InvoiceListQuerySchema.parse(query);

    // List invoices
    const result = await listInvoices({
      userId,
      status: validated.status,
      limit: validated.limit,
      offset: validated.offset,
    });

    const duration = Date.now() - startTime;
    logApiResponse(method, path, 200, duration, userId);

    return NextResponse.json(result);
  } catch (error) {
    return handleApiError(error);
  }
}
