import { z } from 'zod';

// ============ INVOICE SCHEMAS ============

export const InvoiceCreateSchema = z.object({
  clientName: z.string().min(1, 'Client name is required').max(100),
  clientEmail: z.string().email('Invalid email address'),
  amount: z.number().positive('Amount must be positive'),
  dueDate: z.string().refine((date) => !isNaN(Date.parse(date)), {
    message: 'Invalid date format',
  }),
  description: z.string().max(500).optional(),
  paymentMethods: z
    .array(z.enum(['bank_transfer', 'card']))
    .min(1, 'At least one payment method required'),
});

export const InvoiceUpdateSchema = z.object({
  clientName: z.string().min(1).max(100).optional(),
  clientEmail: z.string().email().optional(),
  amount: z.number().positive().optional(),
  dueDate: z.string().optional(),
  description: z.string().max(500).optional(),
  status: z
    .enum(['draft', 'sent', 'paid', 'overdue', 'in_collections', 'disputed', 'cancelled'])
    .optional(),
  paymentMethods: z.array(z.enum(['bank_transfer', 'card'])).optional(),
});

// ============ PAYMENT CONFIRMATION SCHEMAS ============

export const ClientConfirmPaymentSchema = z.object({
  confirmationToken: z.string().uuid(),
  amount: z.number().positive('Amount must be positive'),
  paymentMethod: z.enum(['bank_transfer', 'card']),
  datePaid: z.string().refine((date) => !isNaN(Date.parse(date)), {
    message: 'Invalid date format',
  }),
  clientNotes: z.string().max(500).optional(),
});

// ============ USER SCHEMAS ============

export const UpdateBankDetailsSchema = z.object({
  accountHolderName: z.string().min(1, 'Account holder name is required').max(100),
  accountNumber: z
    .string()
    .regex(/^\d{8}$/, 'Account number must be 8 digits'),
  sortCode: z
    .string()
    .regex(/^\d{2}-?\d{2}-?\d{2}$/, 'Sort code must be in format XX-XX-XX'),
  bankName: z.string().min(1, 'Bank name is required').max(100),
});

export const UpdateNotificationPreferencesSchema = z.object({
  emailNotifications: z.boolean(),
  inAppNotifications: z.boolean(),
  quietHoursStart: z
    .string()
    .regex(/^([01]\d|2[0-3]):([0-5]\d)$/, 'Invalid time format (HH:MM)'),
  quietHoursEnd: z
    .string()
    .regex(/^([01]\d|2[0-3]):([0-5]\d)$/, 'Invalid time format (HH:MM)'),
  notificationTypes: z.array(z.string()),
  onVacation: z.boolean(),
  vacationUntil: z.string().optional(),
});

export const UpdateProfileSchema = z.object({
  name: z.string().min(1).max(100).optional(),
  businessName: z.string().max(100).optional(),
  businessType: z.enum(['freelancer', 'agency', 'consultant']).optional(),
  timezone: z.string().optional(),
  language: z.enum(['en', 'es', 'fr']).optional(),
});

// ============ REFERRAL SCHEMAS ============

export const ProcessReferralSchema = z.object({
  referralCode: z.string().length(8, 'Invalid referral code'),
});

// ============ WEBHOOK SCHEMAS ============

export const StripeWebhookSchema = z.object({
  type: z.string(),
  data: z.object({
    object: z.any(),
  }),
});

export const ClerkWebhookSchema = z.object({
  type: z.string(),
  data: z.any(),
});

// ============ QUERY PARAMS SCHEMAS ============

export const PaginationSchema = z.object({
  limit: z
    .string()
    .optional()
    .transform((val) => (val ? parseInt(val, 10) : 10))
    .refine((val) => val > 0 && val <= 100, {
      message: 'Limit must be between 1 and 100',
    }),
  offset: z
    .string()
    .optional()
    .transform((val) => (val ? parseInt(val, 10) : 0))
    .refine((val) => val >= 0, {
      message: 'Offset must be non-negative',
    }),
});

export const InvoiceListQuerySchema = PaginationSchema.extend({
  status: z
    .enum(['draft', 'sent', 'paid', 'overdue', 'in_collections', 'disputed', 'cancelled'])
    .optional(),
});

// Type inference helpers
export type InvoiceCreateInput = z.infer<typeof InvoiceCreateSchema>;
export type InvoiceUpdateInput = z.infer<typeof InvoiceUpdateSchema>;
export type ClientConfirmPaymentInput = z.infer<typeof ClientConfirmPaymentSchema>;
export type UpdateBankDetailsInput = z.infer<typeof UpdateBankDetailsSchema>;
export type UpdateNotificationPreferencesInput = z.infer<
  typeof UpdateNotificationPreferencesSchema
>;
export type UpdateProfileInput = z.infer<typeof UpdateProfileSchema>;
export type InvoiceListQueryInput = z.infer<typeof InvoiceListQuerySchema>;
